/* Projects section */

.image-project {
    text-align: center;
    position: relative;
    opacity: 0;
    transition: opacity .5s .25s ease-out;
}

.image-project-right {
    text-align: right;
    position: relative;
    opacity: 0;
    transition: opacity .5s .25s ease-out; 
}

.image-project-right.is-visible {
    opacity: 1;
}

.image-project.is-visible {
    opacity: 1;
}

.image-projects {
    width: 95%;
}

.title-project {
    position: absolute;
    font-size: 3.5rem;
    top: -5%;
    left: 45%;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project.is-visible {
    transform: translate(0px, -15px);   
    transition-delay: 300ms;  
}

.title-project-left {
    position: absolute;
    font-size: 3.5rem;
    top: 2%;
    left: 5%;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project-left.is-visible {
    transform: translate(0px, -15px);   
    transition-delay: 300ms;  
}

.link-project {
    position: absolute;
    top: 6%;
    left: 60%;
    font-weight: 700;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 1.5rem;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project.is-visible {
    transform: translate(0px, -15px);   
    transition-delay: 500ms; 
}

.link-project:hover {
    text-decoration: underline;
}

.link-project-left {
    position: absolute;
    top: 12%;
    left: 32%;
    font-weight: 700;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 1.5rem;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project-left.is-visible {
    transform: translate(0px, -15px);   
    transition-delay: 500ms; 
}

.link-project-left:hover {
    text-decoration: underline;
}


.grey-color {
    color: #242424;
}



