  /* KeyFrames */
  
  @keyframes opacity {
    0% {
      transform: translate(100px, -250px);
      opacity: 0;
    }
    50%{
      transform: translate(0px, 0px);
      opacity: 0.5;
    }
    100% {
      transition: transform 0.2s;
      opacity: 1;
    }
  }


/* form */

.contact-me-container {
    height: 95vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    transition: opacity .5s .25s ease-out;
}

.contact-me-container.is-visible {
    opacity: 1;
}

.contact-me-title {
    font-size: 5rem;
    padding: 10px 5px 15px 20px;
    color: #354f52;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
}


.contact-me-title.is-visible {
    transform: translate(0px, -10px);  
}

.contact-me-subtitle {
    padding: 5px 0px 10px 25px;
    font-size: 1.5rem;
    text-decoration: none;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 300ms;
}

.contact-me-subtitle.is-visible {
    transform: translate(0px, -10px);  
}

.contact-me-subtitle:hover {
    font-weight: 800;
}

.italic {
    font-style: italic;
}


/* Media Queries for IPAD */

@media (min-width: 768px) and (max-width: 1024px) {
    .contact-me-title {
        font-size: 8rem;
    }
    
    .contact-me-subtitle {
        font-size: 1.7rem;
    }
  }

  /* Media Queries for Desktop */

  @media (min-width: 1025px) and (max-width: 1280px) {
    .contact-me-title {
        font-size: 8rem;
        text-align: center;
    }
    
    .contact-me-subtitle {
        font-size: 1.7rem;
        text-align: center;
    }
  }

  @media (min-width: 1281px) {
    .contact-me-title {
        font-size: 8rem;
        text-align: center;
    }
    
    .contact-me-subtitle {
        font-size: 1.7rem;
        text-align: center;
    }
  }


.form {
    background-color: grey;
    background-color: white;
    padding: 15px;
}
.form__item {
    padding-bottom: 10px;
}
.form__label {
    font-size: 14px;
    color: #54585a;
}
.form__label--asterisk {
    color:  #14d9c4;
}
.form__input {
    display: block;
    font-size: 14px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 290px;
    height: 45px;
    background-color: white;
    padding: 15px;
    &.form__text {
        height: 90px;
    }
    @media all and (min-width:768px) {
        width: 442px;
    }
    @media all and (min-width:1200px) {
        width: 601px;
    }
}
.form__input--email {
    @media all and (min-width:1200px){
        width: 395px;
}
}
.form__input--telephone {
    @media all and (min-width:1200px){
        width: 162px;
}
}
.form__item--telephone {
    @media all and (min-width:1200px){
        display: inline-block;
        padding-left: 39px;
}
}
.form__item--email {
    @media all and (min-width:1200px){
        display: inline-block;
}
}
.button__container{
    display: flex;
    justify-content: flex-end;
}
.button {
    width: 290px;
    height: 45px;
    border-radius: 4px;
    border: none;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;
    margin: 20px 0px 60px 0px;
    transition: background-color ease 0.8s;
    &:hover{
        background-color: #14d9c4;
    }
    @media all and (min-width:768px)
    {
        width: 206px;
    }
    @media all and (min-width:1200px)
    {
        width: 278px;
    }
}

.title-contact {
    width: 100vw;
    text-align: center;
    padding: 0px 0px 0px 0px;
    margin-top: 0px;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 500;
    font-size: 22px;
    color: #4A4A4A;
    transform: translateY(-100px);

    @media (max-width: 374px) {
        font-size: 20px;
    }

    @media (min-width: 768px) and (max-width: 1024px){
        font-size: 32px;
    }

    @media (min-width: 1024px) {
        font-size: 42px;
    }
}

.contact__text {
    width: 75vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: justify;
    color: $lightGrey;
    font-size: 14px;
    font-weight: 500;
    text-decoration: none;
    padding: 0px;
    margin: 0px 0px 0px 50px;
    transform: translateY(-70px);

    @media (max-width: 374px) {
        font-size: 12px;
    }

    @media (min-width: 768px) and (max-width: 1024px){
        font-size: 30px;
        margin: 0px 0px 0px 100px;
    }

    @media (min-width: 1024px) {
        font-size: 28px;
    }
}
