.page__main-title {
  width: 100%;
  margin-bottom: 15px;
  font-size: 31px;
  text-transform: uppercase;
}/* Dropdown Menu */.dropdown-menu {
  background-color: #242424;
}

.container-logo-exit {
  display: flex;
  justify-content: flex-end;
}

.menu-exit {
  width: 35px;
  padding: 10px;
}

.dropdown-menu-list {
  text-align: center;
  padding: 220px 0px 220px 0px;
}

.dropdown-menu-link {
  font-size: 5rem;
  font-weight: bold;
  margin: 125px 0px 30px 0px;
  color: #efefef;
  text-decoration: none;
}

.dropdown-menu-link:hover {
  color: #2CEAA3;
  font-size: 5.4rem;
}/* footer */.wrapper {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

.page-body {
  flex-grow: 1;
}

.footer {
  max-height: 60px;
  height: auto;
  border-top: 1px solid grey;
  flex-shrink: 0;
  margin: 0px 0px 0px 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  background-image: linear-gradient(-90deg, #2B2D42, #565977);
  width: 100%;
  display: inline-block;
  position: fixed;
  bottom: 0;
}

.footer.is-visible {
  opacity: 1;
}

.social-icon {
  width: 24px;
  margin: 20px 0px 0px 5px;
  padding: 5px 0px 5px 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.social-icon.is-visible {
  transform: translate(0px, -25px);
  transition-delay: 300ms;
}

.container-footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: 0px 0px 0px 0px;
  justify-content: flex-end;
  margin-left: -25px;
  transform: translateY(-40px);
}

.social-link {
  text-decoration: none;
  padding-bottom: 10px;
  font-size: 1.2rem;
  padding-right: 15px;
}

.social-link:hover {
  text-decoration: line-through;
}

.title-footer {
  font-size: 1.35rem;
  padding-top: 0px;
  padding-bottom: 5px;
  color: whitesmoke;
}

.subtitle-footer {
  font-size: 1.25rem;
  flex-shrink: 0;
  color: whitesmoke;
  padding: 0px 0px 0px 0px;
  margin-top: 4px;
  margin-bottom: 0px;
}

.mail-footer {
  font-size: 1.15rem;
  text-decoration: none;
  padding-bottom: 10px;
  font-weight: 700;
  color: whitesmoke;
}

.footer-credits-text {
  font-size: 1.25rem;
  flex-shrink: 0;
  color: whitesmoke;
  padding: 0px 0px 0px 0px;
  margin-top: 0px;
  margin-bottom: 4px;
}/* Media Queries for IPAD *//* Media Queries for Desktop *//* Main */.project-container {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 70px 30px 0px 30px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-container.is-visible {
  opacity: 1;
}/* Title Project */.project-title {
  font-size: 1.6rem;
  color: #c53a13;
  letter-spacing: 1px;
  font-weight: 800;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-title.is-visible {
  transform: translate(0px, -10px);
}

.project-subtitle {
  font-size: 2.6rem;
  padding-bottom: 20px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-subtitle.is-visible {
  transform: translate(0px, -10px);
}

.back-project-container {
  display: flex;
  align-items: center;
  margin-top: -23px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  z-index: 10;
}

.back-project.is-visible {
  transform: translate(0px, -10px);
}

.arrow-img {
  width: 15px;
  padding-right: 5px;
}/* Horizontal image */.cover-image {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.cover-image.is-visible {
  transform: translate(0px, -10px);
}/* Page section left */.project-page-section {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section.is-visible {
  opacity: 1;
}

.project-img-cover {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-cover.is-visible {
  transform: translate(0px, 45px);
  padding-bottom: 0px;
}

.text-container-section {
  position: absolute;
  width: 50vw;
  top: -3%;
  left: 50%;
}

.text-section-rigth {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 15px 30px 15px 15px;
}

.text-section-full {
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  padding: 15px 80px 15px 80px;
}/* Technologies section */.technologies-container {
  padding: 0px 30px 0px 30px;
  text-align: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.technologies-container.is-visible {
  opacity: 1;
}

.title-technologies {
  font-size: 2rem;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.title-technologies.is-visible {
  transform: translate(0px, -10px);
}

.logos-technologies-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 300ms;
}

.logos-technologies-container.is-visible {
  transform: translate(0px, -10px);
}

.technologies-icon {
  width: 25px;
  padding: 5px;
}/* Section Right */.project-page-section-right {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  text-align: right;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-page-section-right {
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-img {
  width: 100vw;
  margin-top: -15px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full.is-visible {
  transform: translate(0px, -10px);
}

.project-img.is-visible {
  transform: translate(0px, -10px);
}

.text-container-section-two {
  position: absolute;
  width: 50vw;
  top: 30%;
}

.text-section {
  font-size: 0.9rem;
  text-align: left;
  font-weight: 500;
  padding: 0px 80px 15px 25px;
}/* Buttons section */.buttons-container {
  display: flex;
  flex-direction: column;
  margin: 10px 0px 50px 0px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.buttons-container.is-visible {
  opacity: 1;
}

.project-button-left {
  font-size: 1.8rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 0px 70px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-left:hover {
  text-decoration: line-through;
}

.project-button-left.is-visible {
  transform: translate(0px, -10px);
}

.project-button-right {
  font-size: 1.4rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 20px 200px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-right:hover {
  text-decoration: line-through;
}

.project-button-right.is-visible {
  transform: translate(0px, -10px);
}

.project-button:hover {
  font-size: 2.3rem;
}/* Media Queries for IPAD *//* Media Queries for Desktop *//* Projects section */.image-project {
  text-align: center;
  position: relative;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.image-project-right {
  text-align: right;
  position: relative;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.image-project-right.is-visible {
  opacity: 1;
}

.image-project.is-visible {
  opacity: 1;
}

.image-projects {
  width: 95%;
}

.title-project {
  position: absolute;
  font-size: 3.5rem;
  top: -5%;
  left: 45%;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.title-project.is-visible {
  transform: translate(0px, -15px);
  transition-delay: 300ms;
}

.title-project-left {
  position: absolute;
  font-size: 3.5rem;
  top: 2%;
  left: 5%;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.title-project-left.is-visible {
  transform: translate(0px, -15px);
  transition-delay: 300ms;
}

.link-project {
  position: absolute;
  top: 6%;
  left: 60%;
  font-weight: 700;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 1.5rem;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.link-project.is-visible {
  transform: translate(0px, -15px);
  transition-delay: 500ms;
}

.link-project:hover {
  text-decoration: underline;
}

.link-project-left {
  position: absolute;
  top: 12%;
  left: 32%;
  font-weight: 700;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 1.5rem;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.link-project-left.is-visible {
  transform: translate(0px, -15px);
  transition-delay: 500ms;
}

.link-project-left:hover {
  text-decoration: underline;
}

.grey-color {
  color: #242424;
}/* Media Queries for IPAD *//* Media Queries for Desktop *//* KeyFrames *//* form */.contact-me-container {
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.contact-me-container.is-visible {
  opacity: 1;
}

.contact-me-title {
  font-size: 5rem;
  padding: 10px 5px 15px 20px;
  color: #354f52;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.contact-me-title.is-visible {
  transform: translate(0px, -10px);
}

.contact-me-subtitle {
  padding: 5px 0px 10px 25px;
  font-size: 1.5rem;
  text-decoration: none;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 300ms;
}

.contact-me-subtitle.is-visible {
  transform: translate(0px, -10px);
}

.contact-me-subtitle:hover {
  font-weight: 800;
}

.italic {
  font-style: italic;
}/* Media Queries for IPAD *//* Media Queries for Desktop */.form {
  background-color: grey;
  background-color: white;
  padding: 15px;
}

.form__item {
  padding-bottom: 10px;
}

.form__label {
  font-size: 14px;
  color: #54585a;
}

.form__label--asterisk {
  color: #14d9c4;
}

.form__input {
  display: block;
  font-size: 14px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 290px;
  height: 45px;
  background-color: white;
  padding: 15px;
}

.form__input.form__text {
  height: 90px;
}

.button__container {
  display: flex;
  justify-content: flex-end;
}

.button {
  width: 290px;
  height: 45px;
  border-radius: 4px;
  border: none;
  color: white;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  text-decoration: none;
  cursor: pointer;
  margin: 20px 0px 60px 0px;
  transition: background-color ease 0.8s;
}

.button:hover {
  background-color: #14d9c4;
}

.title-contact {
  width: 100vw;
  text-align: center;
  padding: 0px 0px 0px 0px;
  margin-top: 0px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 500;
  font-size: 22px;
  color: #4A4A4A;
  transform: translateY(-100px);
}

.contact__text {
  width: 75vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: justify;
  color: #596A62;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  padding: 0px;
  margin: 0px 0px 0px 50px;
  transform: translateY(-70px);
}

